* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
:root {
    --header-height: 9.6rem;
    --footer-height: 3.6rem;
}

html {
    font-size: 62.5%;
}

body {
    font-family: "Rubik", sans-serif;
    line-height: 1;
    font-weight: 400;
    color: #ddd;
    position: relative;
    z-index: 0;
    overflow-x: hidden;
    max-width: 100vw;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    justify-content: space-between;
}
body::before {
    content: "";
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    background-image: url("./assets/img/background-image-blurred.jpg");
    background-size: cover;
    z-index: -100;
}

.container {
    max-width: 140rem;
    padding: 0 3.2rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

.btn {
    font-size: 1.6rem;
    display: inline-block;
    padding: 1rem 2rem;
    font-weight: 500;
    border-radius: 9px;
    border: none;
    cursor: pointer;
    transition: all 0.3s;
    letter-spacing: 1px;
}
.btn-primary,
.btn-primary:link,
.btn-primary:visited {
    background-color: #13253a;
    color: #fff;
    border: 1px solid #fff;
}

.btn-primary:hover {
    background-color: #0b1623;
}

.btn-inactive {
    cursor: auto;
    opacity: 0.3;
    background-color: #13253a;
    color: #ddd;
}

.blur > :not(#modal) {
    filter: blur(5px);
    pointer-events: none;
    overflow: hidden;
}

.btn-secondary,
.btn-secondary:link,
.btn-secondary:visited {
    background-color: #fff;
    color: #13253a;
    border: 1px solid #13253a;
}

.btn-secondary:hover {
    background-color: #e6e6e6;
}

@media (max-width: 84em) {
    html {
        font-size: 56.25%;
    }
    .container {
        padding: 0 4.8rem;
    }
}

@media (max-width: 75em) {
    html {
        font-size: 50%;
    }
}

@media (max-width: 59em) {
    .container {
        padding: 0 6.4rem;
    }
}

@media (max-width: 44em) {
    .btn {
        font-size: 1.4rem;
        padding: 0.8rem 1.6rem;
    }
}

@media (max-width: 34em) {
    .container {
        padding: 0 2.4rem;
    }
}
